<script>
    import MainPageHeader from '../components/MainPageHeader.vue';
    import BankTabber from '../components/bank/BankTabber.vue';
    import BankRow from '../components/bank/BankRow.vue';

    export default {
        name: 'BankPage',
        components: {
            MainPageHeader,
            BankTabber,
            BankRow,
        },
        data() {
            return {
                activeTab: 0,
                selectedPoint: [ 0, 0 ],
            };
        },
        mounted() {
        },
        methods: {
            selectPoint(tabIndex, pointIndex) {
                const points = this.selectedPoint.slice();

                points[tabIndex] = pointIndex;

                this.selectedPoint = points;
            }
        },
    };
</script>

